<template>
  <div
    class="page-box"
    v-if="currentProject"
  >
    <div class="page-box-title">
      <h3>
        <div class="label">Project</div>
        {{currentProject.data().projectName}}
      </h3>
      <div class="flex-divider"></div>
      <!-- <base-button
        type="secondary"
        @click="openAssemblyChecklist"
      >
        <base-icon iconRef="checkmark-circle" />
      </base-button> -->
      <base-button
        type="secondary"
        @click="openEditModal"
      >
        <base-icon iconRef="edit" />
      </base-button>
      <base-button
        type="secondary"
        @click="closeCurrentProject"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="page-box-content values">
      <div
        class="value-group"
        v-if="currentProject.data().description != ''"
      >
        <div class="label">Beschrijving</div>
        <div class="value">{{currentProject.data().description}}</div>
      </div>
      <div class="value-row">
        <div class="value-group">
          <div class="label">Adres</div>
          <div class="value">{{`${currentProject.data().address.city}, ${currentProject.data().address.street} ${currentProject.data().address.houseNumber}, ${currentProject.data().address.postCode}`}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    currentProject: function() {
      return this.$store.getters.getCurrentClientPage.project;
    }
  },
  methods: {
    closeCurrentProject: function() {
      this.$router.push(`/klant/${this.currentProject.data().belongsTo}`);
    },
    openAssemblyChecklist: function() {
      this.$store.commit("setModalShown", {
        dataType: "assemblyChecklist",
        modalType: "view",
        shown: true
      });
    },
    openEditModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "project",
        modalType: "edit",
        shown: true
      });
    }
  }
};
</script>

<style scoped>
</style>


