<template>
  <div
    class="page-box accent"
    v-if="currentClient"
  >
    <div class="page-box-title">
      <h3>
        <div class="label">Klant</div>
        {{currentClient.data().clientName}}
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="openEditModal"
      >
        <base-icon iconRef="edit" />
      </base-button>
      <base-button
        type="secondary"
        @click="closeCurrentClient"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="page-box-content values">
      <div
        class="value-group"
        v-if="currentClient.data().description != ''"
      >
        <div class="label">Beschrijving</div>
        <div class="value">{{currentClient.data().description}}</div>
      </div>
      <div class="value-row">
        <div class="value-group">
          <div class="label">Contactpersoon</div>
          <div class="value">{{`${currentClient.data().contactFirstName} ${currentClient.data().contactLastName}`}}</div>
        </div>
        <div
          class="value-group"
          v-show="currentClient.data().contactEmail.length > 0"
        >
          <div class="label">E-mail</div>
          <div class="value">
            <a :href="`mailto:${currentClient.data().contactEmail}`">{{currentClient.data().contactEmail}}</a>
          </div>
        </div>
        <div
          class="value-group"
          v-show="currentClient.data().contactTel.length > 0"
        >
          <div class="label">Telefoonnummer</div>
          <div class="value">
            <a :href="`tel:${currentClient.data().contactTel}`">{{currentClient.data().contactTel}}</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    currentClient: function() {
      return this.$store.getters.getCurrentClientPage.client;
    }
  },
  methods: {
    closeCurrentClient: function() {
      this.$router.push("/klanten/");
    },
    openEditModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "edit",
        shown: true
      });
    }
  }
};
</script>


